@font-face {
  font-family: "Arial";
  src: url("../fonts/arial.woff2") format("woff2"), url("../fonts/arial.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato-bold.woff2") format("woff2"), url("../../fonts/lato-bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato-regular.woff2") format("woff2"), url("../../fonts/lato-regular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

html {
  font-family: "Arial", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 300px;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1340px;
  margin: 0 auto;
  padding: 0 80px;
}
@media (max-width: 1339px) {
  .container {
    padding: 0 60px;
  }
}
@media (max-width: 1019px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 699px) {
  .container {
    padding: 0 20px;
  }
}

.main-header {
  position: relative;
  overflow: hidden;
  background-image: url("../img/background/header-back.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(138, 123, 96, 0.67);
  color: #ffffff;
}
@media (min-width: 1024px) and (min-resolution: 144dpi), (min-width: 1024px) and (min-resolution: 1.5dppx) {
  .main-header {
    background-image: url("../img/background/header-back@2x.jpg");
  }
}
@media (max-width: 1019px) {
  .main-header {
    background-position: bottom;
  }
}

.main-header__decor-wrapper {
  max-width: 363px;
  position: absolute;
  right: 0;
  bottom: 0;
  transform: translate(0%, 28%);
}
@media (max-width: 1019px) {
  .main-header__decor-wrapper {
    display: none;
  }
}

.main-header__decor {
  width: 100%;
  height: 100%;
}

.logo__image {
  width: 100%;
  height: 100%;
}

@media (max-width: 699px) {
  .main-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.main-nav__logo {
  max-width: 267px;
  display: inline-block;
  vertical-align: middle;
}
@media (max-width: 1019px) {
  .main-nav__logo {
    margin: 0 0 0 20px;
  }
}
@media (max-width: 699px) {
  .main-nav__logo {
    max-width: 167px;
    margin: 0;
  }
}

.main-nav__description {
  margin: 110px 0 213px;
  max-width: 550px;
}
@media (max-width: 1019px) {
  .main-nav__description {
    margin: 130px 0 182px;
  }
}
@media (max-width: 699px) {
  .main-nav__description {
    margin: 50px 0 94px;
    max-width: 305px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media (max-width: 319px) {
  .main-nav__description {
    margin: 49px 0 149px;
  }
}

.main-nav__description h2 {
  margin: 0;
  font-family: "Arial", "Arial", sans-serif;
  font-weight: 400;
  font-size: 72px;
  line-height: 80px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 699px) {
  .main-nav__description h2 {
    font-size: 36px;
    line-height: 40px;
  }
}
@media (max-width: 319px) {
  .main-nav__description h2 {
    font-size: 24px;
    line-height: 24px;
  }
}

.main-nav__description p {
  margin: 15px 0 0 0;
  font-family: "Arial", "Arial", sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 36px;
  color: #ffffff;
}
@media (max-width: 1019px) {
  .main-nav__description p {
    margin: 27px 0 0 0;
  }
}
@media (max-width: 699px) {
  .main-nav__description p {
    margin: 20px 0 0 0;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
  }
}

.preview__container {
  margin: 100px auto 0;
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
@media (max-width: 1019px) {
  .preview__container {
    margin: 80px 0 0 0;
    gap: 20px;
  }
}
@media (max-width: 699px) {
  .preview__container {
    margin: 61px 0 0 0;
    flex-direction: column-reverse;
    margin-bottom: 50px;
  }
}
@media (max-width: 319px) {
  .preview__container {
    margin: 50px 0 0 0;
    margin-bottom: 50px;
  }
}

.preview__video {
  width: 100%;
  margin: 37px 0 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
@media (max-width: 1019px) {
  .preview__video {
    margin: 59px 0 0 0;
  }
}
@media (max-width: 699px) {
  .preview__video {
    margin: 71px 0 0 0;
  }
}
@media (max-width: 319px) {
  .preview__video {
    margin: 28px 0 0 0;
  }
}

.preview__video iframe {
  max-width: 545px;
  max-height: 365px;
}
@media (max-width: 1019px) {
  .preview__video iframe {
    max-width: 100%;
    max-height: 100%;
    width: 100%;
    height: 29vw;
  }
}
@media (max-width: 699px) {
  .preview__video iframe {
    height: 59vw;
    margin-left: 28px;
  }
}
@media (max-width: 319px) {
  .preview__video iframe {
    height: 58vw;
    margin-left: 0;
  }
}

.preview__video-background {
  position: relative;
  width: 100%;
  max-width: 545px;
  max-height: 365px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
}
.preview__video-background::before {
  position: absolute;
  content: "";
  background-color: #f7eee5;
  top: -37px;
  right: 35px;
  bottom: 37px;
  left: -35px;
  z-index: -1;
}
@media (max-width: 1019px) {
  .preview__video-background::before {
    top: -27px;
    bottom: 32px;
    right: 39px;
    left: -40px;
  }
}
@media (max-width: 699px) {
  .preview__video-background::before {
    top: -43px;
    left: -30px;
    right: 49px;
  }
}
@media (max-width: 319px) {
  .preview__video-background::before {
    top: -20px;
    left: -16px;
    right: 25px;
    bottom: 14px;
  }
}
@media (max-width: 1019px) {
  .preview__video-background {
    aspect-ratio: 405/271;
    max-width: 405px;
  }
}
@media (max-width: 699px) {
  .preview__video-background {
    max-width: 100%;
    margin-left: 29px;
    aspect-ratio: 541/361;
  }
}
@media (max-width: 319px) {
  .preview__video-background {
    margin-left: 0;
  }
}

.preview__video-player {
  display: none;
}

.preview__video-poster {
  width: 100%;
  height: auto;
}
@media (max-width: 699px) {
  .preview__video-poster {
    max-width: 100%;
  }
}

.preview__video-btn {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  max-height: 44px;
  max-width: 67px;
  border: none;
  background-color: inherit;
  background-image: url("../img/svg/video-btn.svg");
  background-repeat: no-repeat;
  top: 50%;
  left: 49%;
  transform: translate(-50%, -50%);
  z-index: 2;
  background-size: contain;
}
@media (max-width: 1019px) {
  .preview__video-btn {
    max-width: 51px;
  }
}
@media (max-width: 699px) {
  .preview__video-btn {
    max-width: 69px;
    top: 48%;
  }
}
@media (max-width: 319px) {
  .preview__video-btn {
    max-width: 32px;
    top: 55%;
    left: 48%;
  }
}

.preview__video--active .preview__video-player {
  display: block;
}

.preview__video--active .preview__video-btn {
  display: none;
}

.preview__video--active .preview__video-poster {
  display: none;
}

.preview__video--active .preview__video-background {
  display: none;
}

.preview__description {
  max-width: 545px;
  width: 100%;
}
@media (max-width: 1019px) {
  .preview__description {
    max-width: 417px;
  }
}
@media (max-width: 699px) {
  .preview__description {
    max-width: 100%;
  }
}

.preview__description-year-wrapper {
  max-width: 148px;
}
@media (max-width: 699px) {
  .preview__description-year-wrapper {
    max-width: 71px;
  }
}

.preview__description-year {
  width: 100%;
  height: 100%;
}

.preview__description h3 {
  margin: 16px 0 0 0;
  font-family: "lato", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: #0a3b93;
}
@media (max-width: 699px) {
  .preview__description h3 {
    margin: 10px 0 0;
    font-size: 8.7px;
    line-height: 2px;
  }
}
@media (max-width: 319px) {
  .preview__description h3 {
    margin: 13px 0 0;
  }
}

.preview__paragraph-wrapper {
  margin: 32px 0 0 0;
}
@media (max-width: 699px) {
  .preview__paragraph-wrapper {
    margin: 23px 0 0 0;
  }
}
@media (max-width: 319px) {
  .preview__paragraph-wrapper {
    margin: 30px 0 0 0;
  }
}

.preview__description p {
  margin: 16px 0 0 0;
  font-family: "Arial", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  color: #6c5f4a;
}
@media (max-width: 699px) {
  .preview__description p {
    margin: 9px 0 0 0;
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 319px) {
  .preview__description p {
    margin: 11px 0 0 0;
  }
}